.list-all {
	border: 2px solid #00b894;
	margin: 80px auto;
	height: auto;
	padding-top: 0;
	overflow: hidden;
	border-radius: 25px;
	box-shadow: 0px 10px 11px -4px #dfe6e9;
	transition: all 1s linear;
	background-color: white;
	max-width: 80%;
}

.list-all-loading-pos {
	margin-top: 120px;
	display: flex;
	justify-content: center;
}

h2 {
	font-size: 2em;
}

.list-all-title {
	margin: 0;
	padding: 5px 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	background-color: white;
	transition: all 0.1 linear;
}

.title {
	margin: 0;
	color: #00b894;
	font-family: "Muli";
	font-weight: 400;
}

.list-all-disabled {
	opacity: 50;
	pointer-events: none;
}

.list-all-add-task {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	background-color: transparent;
	transition: all 0.1 linear;
}

.list-all-add-task > button {
	text-align: left;
	color: white;
	font-family: "Muli";
	font-weight: 800;
	font-size: 1.5em;
	outline: none;

	width: 100%;
	height: 100%;
	background-color: #00b894;
	border: 0 none transparent;
	padding-left: 20px;
	padding-top: 20px;
	padding-bottom: 20px;

	transition: all 0.1s linear;
}

.list-all-add-task > button:hover {
	text-decoration: underline;
	background-color: #dfe6e9;
	color: #00b894;
}

/* smaller screens */
@media only screen and (max-width: 900px) {
	.list-all {
		max-width: 99%;
		margin: 80px auto;
	}

	h2 {
		font-size: 1em;
	}

	.list-all-add-task > button {
		font-size: 18px;
		padding-left: 10px;
		padding-top: 20px;
		padding-bottom: 20px;
	}
}

/* ***********BACKUP************* */

.checkbox-container {
  display: inline-block;
}

.checkbox-container label {
  cursor: pointer;
  border-radius: 50%;
  overflow: hidden;
}

.checkbox-container input[type="checkbox"] {
  cursor: pointer;
  opacity: 0;
  width: 1.5em;
  height: 1.5em;
  position: absolute;
  display: none;
}

.checkbox-container label::before {
  font-family: FontAwesome;
  content: "\f00c";
  font-size: 1em;
  color: #b2bec3;
  width: 1em;
  height: 1em;
  border-radius: 30%;
  border: 0.03em solid #b2bec3;
  transition: color 0.5s;
  transition: background-color 0.5s;
}

/* .checkbox-container label:hover::before,
.checkbox-container input[type="checkbox"]:hover + label::before {
  background-color: #35e07d;
} */

/* .checkbox-container input[type="checkbox"]:focus + label::before {
  box-shadow: 0 0 20px black;
} */

.checkbox-container input[type="checkbox"]:disabled + label,
.checkbox-container input[type="checkbox"]:disabled {
  color: #aaa;
  cursor: default;
}

.checkbox-container input[type="checkbox"]:checked + label::before {
  content: "\f00c";
  color: #fcfcfc;
  background-color: #00b894;
}

.checkbox-container input[type="checkbox"]:disabled + label::before {
  background-color: #2d3436;
  border-color: #dfe6e9;
}

.list {
  position: relative;
  margin: 10px auto;
  max-width: 98%;
  height: 120px;
  overflow: hidden;
  display: flex;
  flex: wrap 1;
  justify-content: flex-start;
  align-items: center;
  font-size: 18px;
  transition: all 0.2s linear;
  border-radius: 25px 25px 25px 25px;
  background-color: #f8f8f8;
  box-shadow: 0px 10px 11px -4px #dfe6e9;
}

.list-bg-done {
  z-index: 0;
  position: absolute;
  width: 0%;
  height: 100%;
  color: #dfe6e9;
}

.list-bg-done-show {
  -webkit-animation: stretchWidth 0.5s;
          animation: stretchWidth 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.list-bg-done-close {
  -webkit-animation: clearWidth 0.5s;
          animation: clearWidth 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.list-disabled {
  width: 0%;
  opacity: 20;
}

.list-add {
  -webkit-animation: FadeIn 0.5s;
          animation: FadeIn 0.5s;
}

.list-delete {
  align-items: left;
  -webkit-animation: FadeOut 0.5s;
          animation: FadeOut 0.5s;
}

.list-col-1 {
  z-index: 1;
  width: 5%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list-col-2 {
  z-index: 1;
  width: 75%;
  height: 90%;
  display: inline-flexbox;
  justify-content: center;
  align-items: center;
}

.list-col-3 {
  z-index: 1;
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list-col-2-input {
  margin-top: 0;
  color: #636e72;
  font-family: "Muli";
  font-weight: 800;
  text-align: left;
  width: 98%;
  height: 30%;
  font-size: 1.8em;
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 10px;
  overflow: hidden;
  border: 0;
  outline: none;
  background-color: transparent;
  text-decoration: none;
  transition: all 0.5s linear;
}

.input-effect {
  display: flex;
  width: 0%;
  height: 2px;
  background-color: #ffeaa7;
  transition: all 0.5s linear;
}

.list-col-2-input:focus ~ .input-effect {
  background-color: #0984e3;
  width: 98%;
}

.list-col-2 input:disabled {
  background-color: transparent;
}

.list-col-2-time {
  text-align: right;
  width: 98%;
  height: 20%;
  border: none;
  background-color: transparent;
  color: #636e72;
  font-family: "Muli";
  font-size: 1em;
  font-weight: 800;
  letter-spacing: 0.1px;
  padding-left: 10px;
  outline: none;
}

.list-col-2-desc {
  width: 98%;
  height: 45%;
  overflow: auto;
  background-color: transparent;
  outline: none;
  border: 0;
  resize: none;
  overflow: auto;
  margin: 0;
  padding: 0 0 0 10px;
  text-align: left;
  font-size: 1em;
  color: #636e72;
  transition: all 0.5s linear;
}

.description-effect {
  display: flex;
  width: 0%;
  height: 2px;
  background-color: #ffeaa7;
  transition: all 0.5s linear;
}

.list-col-2-desc:focus ~ .description-effect {
  background-color: #00b894;
  width: 98%;
}

.list-col-3 > .btn {
  width: 50%;
  height: 100%;
  border: 0 none transparent;
  outline: none;
  text-decoration: none;
  transition-property: color, background-color;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  font-size: 0.9em;
  font-family: "Muli";
  font-weight: 800;
}

.btn:hover {
  text-decoration: underline;
}

.btn:active {
  background-color: #b2bec3;
}

.btn-edit {
  background-color: #2d3436;
  color: #00b894;
}

.btn-done {
  background-color: #00b894;
  color: #2d3436;
}

.btn-delete {
  background-color: #d63031;
  color: #dfe6e9;
}

.btn-icon {
  margin-right: 5px;
}

/* smaller screens */
@media only screen and (max-width: 900px) {
  .list {
    height: 100px;
  }

  .list:hover {
    height: 120px;
  }

  .list-col-1 {
    width: 10%;
    margin: 0 auto;
    padding-left: 5px;
    display: flex;
    justify-content: center;
  }

  .list-col-2 {
    width: 80%;
    font-size: 12px;
  }

  .list-col-2-input {
    width: 95%;
    font-size: 16px;
  }

  .list-col-2-desc {
    width: 95%;
    font-size: 12px;
  }

  .list-col-2-time {
    padding-top: 5px;
    padding-bottom: 5px;
    width: 95%;
    font-size: 12px;
  }

  .input-effect {
    margin-bottom: 2px;
  }

  .list-col-3 {
    width: 20%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  .list-col-3 > .btn {
    width: 100%;
    padding: 0;
    font-size: 10px;
  }

  .btn-text {
    display: none;
  }

  .btn-icon {
    font-size: 16px;
    margin: 10px;
  }

  .btn-edit:hover {
    background-color: #636e72;
  }

  .btn-done:hover {
    background-color: #00b894;
  }

  .btn-delete:hover {
    background-color: #ff7675;
  }
}

/*** CSS Animation ***/

@-webkit-keyframes FadeIn {
  from {
    opacity: 0;
    transform: translateX(-1000px);
  }
  to {
    opacity: 100;
    transform: translateX(0px);
  }
}

@keyframes FadeIn {
  from {
    opacity: 0;
    transform: translateX(-1000px);
  }
  to {
    opacity: 100;
    transform: translateX(0px);
  }
}

@-webkit-keyframes FadeOut {
  from {
    opacity: 100;
    transform: translateX(0px);
  }
  to {
    opacity: 0;
    transform: translateX(-1000px);
  }
}

@keyframes FadeOut {
  from {
    opacity: 100;
    transform: translateX(0px);
  }
  to {
    opacity: 0;
    transform: translateX(-1000px);
  }
}

@-webkit-keyframes stretchWidth {
  0% {
    width: 0%;
    background-color: #ffeaa7;
  }
  50% {
    background-color: #81ecec;
  }
  100% {
    width: 100%;
    background-color: #00b894;
  }
}

@keyframes stretchWidth {
  0% {
    width: 0%;
    background-color: #ffeaa7;
  }
  50% {
    background-color: #81ecec;
  }
  100% {
    width: 100%;
    background-color: #00b894;
  }
}

@-webkit-keyframes clearWidth {
  0% {
    width: 100%;
    background-color: #00b894;
  }
  50% {
    background-color: #81ecec;
  }
  100% {
    width: 0%;
    background-color: #ffeaa7;
  }
}

@keyframes clearWidth {
  0% {
    width: 100%;
    background-color: #00b894;
  }
  50% {
    background-color: #81ecec;
  }
  100% {
    width: 0%;
    background-color: #ffeaa7;
  }
}

