  }
  to {
    height: 60%;
  }
}

@keyframes SlideIn {
  from {
    opacity: 1;
    height: 0%;
  }
  to {
    height: 60%;
  }
}

@-webkit-keyframes SlideOut {
  from {
    height: 60%;
  }
  to {
    height: 0%;
    opacity: 0;
  }
}

@keyframes SlideOut {
  from {
    height: 60%;
  }
  to {
    height: 0%;
    opacity: 0;
  }
}

